@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
/* Reset */
html, body, main, div, section, article,
h1, h2, h3, h4, h5, p, strong, em, a, li, ul, 
ol, nav, header, span, label, input, textarea, button,
select {
  margin: 0;
  border: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  font: inherit;
  text-decoration: inherit;
  text-align: inherit;
  vertical-align: inherit;
  color: inherit;
  background: transparent;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

label {
  display: block;
}

button {
    cursor: pointer;
}

/* QUESTION: not sure how necessary this is. From CSS Tricks */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; 
  font-size: 100%; 
  line-height: 1; 
}

button,
input { 
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
body {
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  color: #242424;
}

/* Navbar */
header {
  height: 60px;
  border-bottom: 1px solid #949494;
  box-shadow: 0px 2px 5px #cacaca;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: inherit;
  margin: 0 1.5rem;
}

nav > a {
  font-size: 2rem;
}

.user-links {
  display: flex;
}

.user-links > li {
  margin-left: 1rem;
  font-size: 1.25rem;
}

/* Main */
main {
  width: 80%;
  margin: 1.5rem auto;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

h2 {
  font-size: 1.3rem;
  margin: 1.2rem 0;
}

/* Expense form */
.form {
  margin: 1.7rem 0;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  flex-wrap: wrap;
}

.form-input {
  margin: 0.4rem 0;
  margin-right: 1.2rem;
  min-width: 0;
}

.form-input label {
  font-weight: 500;
}

.form-input > input, select {
  margin-top: 0.6rem;
  border: 1px solid #797979;
  padding: 0.5rem;
  border-radius: 6px;
  min-height: 2.6rem;
}

input:focus, select:focus {
  border: 2px solid #6642f5;
}

/* Expense Table */
.table {
  width: 100%;
  margin: 1.5rem auto;
  border: 1px solid #424242;
  border-radius: 6px;
}

.table-headers th {
  padding: 0.7rem;
  text-align: left;
}

.table td {
  padding: 0.7rem;
}

.table-caret {
  margin-left: 0.4rem;
}

/* buttons */
form button {
  background-color: #65e6ca;
  padding: 0.3rem 1.5rem;
  border-radius: 6px;
  height: 45px;
  font-weight: 500;
  margin-left: 1rem;
  margin-bottom: 0.3rem;
}

.filter-bar {
  display: flex;
  align-items: baseline;
}

.expense-filter {
  margin-left: 1rem;
}

.secondary-button {
  background-color: #dadada;
  padding: 0.3rem;
  border-radius: 3px;
  margin-right: 0.6rem;
}

/* Chart */
#chart-container {
  border: 2px solid #797979;
  border-radius: 6px;
}


#expense-chart {
  max-height: 400px;
  position: relative;
}

.alert {
  margin: 2rem 0;
}
